	module.exports = {"data":{"markdownRemark":{"html":"<p>You can attach classes to HTML elements by using <code>class=\"your-class-here\"</code> within the relevant element’s opening tag.</p>\n<p>CSS selectors require a period at the beginning like this: <code>.blue-text { color: blue; }</code>, but that class declaration in the HTML doesn’t use a period, like this: <code>&#x3C;h2 class=\"blue-text\">CatPhotoApp&#x3C;h2></code>.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code><style>\n  .red-text {\n    color: red;\n  }\n</style>\n\n<h2 class=\"red-text\">CatPhotoApp</h2>\n\n<p class=\"red-text\">Kitty ipsum dolor sit amet, shed everywhere shed everywhere stretching attack your ankles chase the red dot, hairball run catnip eat the grass sniff.</p></code></pre>\n      </div>","fields":{"slug":"/certificates/style-multiple-elements-with-a-css-class/"},"frontmatter":{"title":"Style Multiple Elements with a CSS Class"}}},"pathContext":{"slug":"/certificates/style-multiple-elements-with-a-css-class/"}}

